.header__body-container {
  padding-top: 25px;
  padding-bottom: 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__phone {
  display: flex;
  align-items: center;
}

.header__phone-text {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.header__phone-image {
  padding: 7px;
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 0px 5.81081px 15.1081px rgba(140, 121, 199, 0.3);
  margin-right: 14px;
}

.header__phone-description {
  font-weight: normal;
  font-size: 13px;
  line-height: 15px;
  color: #aeaeae;
  margin-bottom: 2px;
}

.header__phone-number {
  font-weight: bold;
  font-size: 24px;
  color: #313131;
  text-decoration: none;
}

.header__search {
  width: 312px;
  margin-right: 20px;
  margin-left: 20px;
  display: flex;
  align-items: center;
  padding: 10px 15px 10px 47px;
  background: #fcfcfc url("../../../img/searchIcon.svg") no-repeat;
  background-position: 7px 50%;
  background-size: 23px 23px;
  box-sizing: border-box;
  border: 1px solid #d9d9d9;
  border-radius: 23px;
  color: #404040;
  font-size: 15px;
  line-height: 18px;
  transition: 0.3s;
}

.header__search:focus {
  border: 1px solid #784e96;
  box-shadow: 0px 0px 7px #8672c34a;
}

.header__search::placeholder {
  font-size: 15px;
  line-height: 18px;
  color: #b3b3b3;
}

.header__statusbar {
  min-width: 184px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__statusbar-item {
  position: relative;
  width: 38px;
  height: 38px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 7px;
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 0px 5.81081px 15.1081px rgba(140, 121, 199, 0.3);
  cursor: pointer;
  transition: 0.4s;
}

.header__statusbar-item:hover {
  box-shadow: 0px 0px 5px rgba(140, 121, 199, 0.3);
}

.header__statusbar-item-quantity {
  position: absolute;
  top: -5px;
  right: -6px;
  min-width: 11px;
  min-height: 11px;
  padding: 3px;
  background-color: #34c900;
  border-radius: 21px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 11px;
  color: #ffffff;
}

.compare {
  font-size: 24px;
}
