.button__purple {
  background: linear-gradient(
      180deg,
      rgba(255, 255, 255, 0) 0%,
      rgba(0, 0, 0, 0.13) 98.96%
    ),
    #8b75c8;
  border-radius: 220px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 16px;
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  font-size: 13px;
  line-height: 15px;
  text-align: center;
  color: #ffffff;
  filter: drop-shadow(0px 8px 30px rgba(139, 117, 200, 0.5));
  transition: 0.3s;
}

.button__purple:hover {
  background-color: #6a599a;
}
