.button__green {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #91cf0d 100%),
    #97d413;
  border: 1px solid #97d413;
  box-sizing: border-box;
  border-radius: 220px;
  padding: 18px 32px;
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  color: #ffffff;
  filter: drop-shadow(0px 6px 12px rgba(0, 0, 0, 0.1));
  transition: 0.3s;
}

.button__green:hover {
  background-color: #8ec713;
}
